.experience_container {
    display: grid;
    /* acá modifico las columnas que quiero */
    grid-template-columns: 1fr 1fr; 
    gap: 2rem;
}
.experience_container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem  5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    width: 90%;
    margin-left: 25px;
}

.experience_container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.experience_container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience_content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}
.experience_details {
    display: flex;
    gap: 1rem;
}
.experience_details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}
@media screen and (max-width: 1024px){
    .experience_container {
        height: 80vh;
        grid-template-columns: 1fr 1fr;
        
        
    }
    .test {
        position: relative;
        top: -40px;
    }
    .experience_container > div {
        width: 100%;
        margin: 0 auto;
        padding: 2rem;
    }
    .experience_content {
        padding: 1rem;
    }
}
@media screen and (max-width: 600px) {
    .experience_container {
        
        position: relative;
        gap: 2.5rem;
        grid-template-columns: 1fr;
        /* padding-top: -160rem; */
        top: -60px;
        height: 190vh;
    }
    .test {
        position: relative;
        top: -60px;
    }
    .experience_container > div {
        width: 90%;
        padding: 2rem 1rem;
    }
}